import React from 'react'
const { Octokit } = require('@octokit/rest')

const octokit = new Octokit({
  auth: '501d530a159fc4fb7f946263b91fc509a11c6d85'
})

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      items: [],
      posts: [],
      downloads: [],
    }
  }

  componentDidMount() {
    window.App = this
    this.octokit = octokit
    this.getData()
  }

  async getData() {
    const res = await this.octokit.repos.getContent({
      owner: 'ryosuzuki',
      repo: 'monitor-db',
      path: 'db.json',
      headers: {
        'If-None-Match': ''
      }
    })
    try {
      let items = JSON.parse(atob(res.data.content))
      items = items.reverse()
      console.log(items)
      const posts = items.filter((item => item.type == 'post'))
      const downloads = items.filter((item => item.type !== 'post'))
      this.setState({ items: items, posts: posts, downloads: downloads })
    } catch (err) {
      console.log(err)
    }
  }

  timeFormat(timestamp) {
    const time = new Date(timestamp)
    let year = time.getFullYear()
    let month = '' + (time.getMonth() + 1)
    let day = '' + time.getDate()
    let hour = '' + time.getHours()
    let min = '' + time.getMinutes()
    if (month.length < 2)
      month = '0' + month
    if (day.length < 2)
      day = '0' + day
    if (hour.length < 2)
      hour = '0' + hour
    if (day.length < 2)
      min = '0' + min
    return `${year}-${month}-${day} ${hour}:${min}`
  }

  render() {
    return (
      <div>
        <title>Monitor</title>
        <div className="ui two column centered grid">
          <div className="column">
            <h1>Monitor</h1>

            <h1>Post</h1>
            <table className="ui unstackable table">
              <thead>
                <tr>
                  <th>Status</th>
                  <th>Time</th>
                  <th>Blog</th>
                </tr>
              </thead>
              <tbody>
                { this.state.posts.map((post, i) => {
                  return (
                    <tr key={ i } className={ post.error ? 'negative' : 'positive' }>
                      <td data-label="error">
                        <i className={ post.error ? 'icon close' : 'icon checkmark' } />
                      </td>
                      <td data-label="timestamp">{ this.timeFormat(post.timestamp) }</td>
                      { !post.error &&
                        <td data-label="blog">{ post.data.item.article }</td>
                      }
                      { post.error &&
                        <td data-label="blog">{ post.data.blog }</td>
                      }
                    </tr>
                  )
                })}
              </tbody>
            </table>

            <h1>downloads</h1>
            <table className="ui unstackable table">
              <thead>
                <tr>
                  <th>Status</th>
                  <th>Time</th>
                  <th>Key</th>
                </tr>
              </thead>
              <tbody>
                { this.state.downloads.map((download, i) => {
                  return (
                    <tr key={ i } className={ download.error ? 'negative' : 'positive' }>
                      <td data-label="error">
                        <i className={ download.error ? 'icon close' : 'icon checkmark' } />
                      </td>
                      <td data-label="timestamp">{ this.timeFormat(download.timestamp) }</td>
                      <td data-label="key">{ download.data.key }</td>
                    </tr>
                  )
                })}
              </tbody>
            </table>

          </div>
        </div>
      </div>
    )
  }
}

export default Index